import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { ToolsProvider } from '../../providers/tools/tools';
import { ReservationServiceProvider } from '../../providers/reservation-service/reservation-service';
import { NotificationProvider } from '../../providers/notification/notification';

/**
 * Generated class for the NotificationDetailsPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-notification-details',
  templateUrl: 'notification-details.html',
})
export class NotificationDetailsPage {
  item: any;

  constructor(public navCtrl: NavController, public navParams: NavParams, private toolsProvider: ToolsProvider, public reservationProvider: ReservationServiceProvider, private notificationProvider: NotificationProvider) {
    this.item = this.navParams.get('item'); // gets item 
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad NotificationDetailsPage');
  }

  formatNotificationDate(serviceDate) {

    let today = new Date(),
      reservaDate = new Date(this.toolsProvider.formatDate(serviceDate)),
      date = '';
    let dateDiff = this.toolsProvider.date_diff_indays(today, reservaDate);
    let dateTextTranslate = this.toolsProvider.fnLanguageTranslate(["common.today", "common.yesterday", "common.dateFormat.long"]); // language translation
    let todayText = '';
    let yesterdayText = '';
    let dateLongFormat = "";
    dateTextTranslate.subscribe((value: string) => {
      todayText = value["common.today"];
      yesterdayText = value["common.yesterday"];
      dateLongFormat = value["common.dateFormat.long"].replace(/\\\\/g, '');
    });
    if (dateDiff == 0) { //today
      date = todayText;
    } else if (dateDiff == -1) { // yesterday
      date = yesterdayText;
    } else {
      //change into date format 
      let dayName: any = this.toolsProvider.getDayName(reservaDate.getDay());
      let monthName: any = this.toolsProvider.getMonthName(reservaDate.getMonth());
      let dayValue: any = reservaDate.getDate();
      let dayModified: any = this.getOrdinalNum(dayValue);
      let year: any = reservaDate.getFullYear();

      dateLongFormat = dateLongFormat.replace("l", dayName).replace("j", dayValue).replace("jS", dayModified).replace("F", monthName).replace("Y", year);
      date = dateLongFormat.replace(/\\/g, '');

    }
    let hours: any = reservaDate.getHours() > 12 ? reservaDate.getHours() - 12 : reservaDate.getHours();
    let am_pm = reservaDate.getHours() >= 12 ? "PM" : "AM";
    hours = hours < 10 ? "0" + hours : hours;
    let minutes = reservaDate.getMinutes() < 10 ? "0" + reservaDate.getMinutes() : reservaDate.getMinutes();
    let time = hours + ":" + minutes + " " + am_pm;

    //add time to date
    date = date + ', ' + time;

    return date;

  }

  getOrdinalNum(n) {
    return n + (n > 0 ? ['th', 'st', 'nd', 'rd'][(n > 3 && n < 21) || n % 10 > 3 ? 0 : n % 10] : '');
  }

  public fnBack() {
    this.navCtrl.pop({ animate: true, animation: 'transition', duration: 500, direction: 'back' });
  }

  getImage(item) {
    let reservationId = this.reservationProvider.objReservation.reservationId;
    let QRImage = this.toolsProvider.getQRImagePath(item.picture, reservationId),
      PushImage = this.toolsProvider.getImagePath(item.picture);

    if ((item.tipo == 'CHECKIN')) {
      console.log("path QRImage: " + QRImage);
      return QRImage;
    } else if (item.tipo == 'MENSAJE_PLANIFICADO' && item.picture != null) {
      console.log("path PushImage: " + PushImage);
      return PushImage;
    }

  }
}



